.app {
  text-align: center;
  font-family: 'Arial', sans-serif;
  max-width: 600px; /* Increased to accommodate larger boards */
  margin: 0 auto;
  padding: 20px;
  background: #faf8ef;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.board {
  display: grid;
  grid-template-columns: repeat(4, 1fr);  /* 4 equal columns */
  grid-template-rows: repeat(4, 1fr);     /* 4 equal rows */
  width: 400px;   /* total fixed board size */
  height: 400px;
  gap: 5px;
  margin: 20px auto;
  /* background: #a81751; */
  padding: 5px;
  border-radius: 5px;
  box-sizing: border-box;
}


.tile {
  width: var(--tile-size); /* Use CSS variable for consistent size */
  height: var(--tile-size); /* Ensure square tiles */
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: calc(0.4 * var(--tile-size)); /* Dynamic font size */
  font-weight: bold;
  color: #776e65;
  background: #cdc1b4;
  border-radius: 3px;
  transition: background 0.2s, transform 0.2s;
}

.tile:hover {
  transform: scale(1.05);
}

.tile-0 { background: #cdc1b4; } /* Empty tile color */
.tile-2 { background: #eee4da; }
.tile-4 { background: #ede0c8; }
.tile-8 { background: #f2b179; }
.tile-16 { background: #f59563; color: #f9f6f2; }
.tile-32 { background: #f67c5f; color: #f9f6f2; }
.tile-64 { background: #f65e3b; color: #f9f6f2; }
.tile-128 { background: #edcf72; color: #f9f6f2; }
.tile-256 { background: #edcc61; color: #f9f6f2; }
.tile-512 { background: #edc850; color: #f9f6f2; }
.tile-1024 { background: #edc53f; color: #f9f6f2; }
.tile-2048 { background: #edc22e; color: #f9f6f2; }

.message {
  font-size: 1.5em;
  margin: 10px 0;
}

.win { color: green; }
.game-over { color: red; }

button {
  margin: 10px;
  padding: 10px 20px;
  margin-top: 30px;
  font-size: 1em;
  cursor: pointer;
  background: #8f7a66;
  color: white;
  border: none;
  border-radius: 5px;
}

.config {
  margin-top: 20px;
}

.instructions {
  margin-top: 20px;
  text-align: left;
  font-size: 0.9em;
  color: #776e65;
}